				this.monthReachList = res.obj.monthReachList;
				// 产品
				this.monthIncrList = res.obj.monthIncrList;
			},
			// 获取用户信息
			mpGetUserInfo() {
				let me = this
				uni.getUserProfile({
					desc: 'desc',
					success: function(infoRes) {
						/* me.userData.headImg = infoRes.userInfo.avatarUrl
						me.$u.api.miniInfoApi({
							encryptedData: infoRes.encryptedData,
							iv: infoRes.iv,
							rawData: infoRes.rawData,
							signature: infoRes.signature
						}) */
						uni.$u.toast(JSON.stringify(infoRes.userInfo))
						console.log('用户 为：', infoRes);
					}
				})
			},
			// 登录
			async toLogin() {
				const res = await new Promise((resolve, reject) => {
					uni.login({
						provider: 'weixin',
						success: async (res) => {
							console.log('微信登录成功', res);
							/* uni.showModal({
								title: '微信登录成功',
								content: 'code' + res.code,
								showCancel: false
							}); */
							/* uni.$u.toast(res.code)
							uni.getUserInfo({
								provider: 'weixin',
								success: function(infoRes) {
									uni.$u.toast(infoRes.userInfo.nickName)
								}
							}); */
							resolve(res)
						},
					});
				})
				const data = await loginApi({
					code: res.code,
				})
				console.log('loginApi：', data);
				if (data.phone && data.auth) {
					this.briefingDay(this.searchDate)
					this.phone = data.phone
					await uni.setStorageSync('phone', data.phone);
				} else if (!data.auth) {
					uni.showModal({
						title: '提示',
						content: '手机号不在白名单，请联系管理员添加~',
						showCancel: false
					});
					// uni.$u.toast('手机号不在白名单，请联系管理员添加~')
					this.phone = null
					await uni.setStorageSync('phone', '');
				}
				await uni.setStorageSync('openid', data.openid);
			},
			// 获取手机号
			async onGetPhoneNumber(result) {
				// console.log('11',result);
				if (result.detail.errMsg !== "getPhoneNumber:ok") {
					// uni.showModal({
					// 	title: '获取用户信息失败',
					// 	content: '错误原因' + result.detail.errMsg,
					// 	showCancel: false
					// });
					return;
				}
				// this.phone = 123
				// await this.toLogin()
				console.log('onGetPhoneNumber', result.detail);
				const openid = await uni.getStorageSync('openid')
				// 手机号接口
				const data = await phoneApi({
					encryptedData: result.detail.encryptedData,
					iv: result.detail.iv,
					openid: openid,
				})
				console.log('phoneApi：', data);

				if (data.phone && data.auth) {
					this.briefingDay(this.searchDate)
					this.phone = data.phone
					await uni.setStorageSync('phone', data.phone);
				} else if (!data.auth) {
					// uni.$u.toast('手机号不在白名单，请联系管理员添加~')
					uni.showModal({
						title: '提示',
						content: '手机号不在白名单，请联系管理员添加~',
						showCancel: false
					});
					this.phone = null
					await uni.setStorageSync('phone', '');
				}
				await uni.setStorageSync('token', data.token);
			},
			// 日新增标签点击
			clickDayIncrList(item) {
				this.dayIncrListInd = item.index
				let minArr = item.histogramData.series[0].data
				let min = Math.min(...minArr);
				console.log(item);
				console.log('item.histogramData', item.histogramData.series[0].data, min);
				if (item.title.indexOf('率') > 0) {
					this.optsDayIncrList.yAxis.data[0] = {
						min: 0,
						max: 100
					}
				} else {
					this.optsDayIncrList.yAxis.data[0] = {
						min: min > 0 ? 0 : min - Math.pow(10, min.toString().length - 2)
					}
				}
			},
			// 产品标签点击
			clickProductIncrList(item) {
				this.productIncrListInd = item.index
				let minArr = item.histogramData.series[0].data
				let min = Math.min(...minArr);
				console.log('item.histogramData', item.histogramData.series[0].data, min);
				this.optsProductIncrList.yAxis.data[0] = {
					min: min > 0 ? 0 : min - Math.pow(10, min.toString().length - 2)
				}
			},

		}
	};
</script>

